.form-control {
  border-radius: 0.125rem;
  &:focus {
    box-shadow: 0 0 0 0.05rem rgba(47, 64, 80, 0.25);
  }
}

.form-group {
  label {
    font-weight: 500;
  }
}

select {
  &:invalid {
    color: #A9ABAE;
  }
  [disabled] {
    color: gray;
  }
  option {
    color: $body-color;
  }
}